.Game {
    display: flex;
    flex-direction: column;
    height: 100vh;
    background-color: black;
    font-family: 'Roboto', sans-serif;
    font-size: 1.5rem;
    color: #333;
    text-align: center;
}

.Game .Game-condition-dead {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    z-index: 1;
    font-size: 4em;
    height: 10%;
    width: 100%;
    font-weight: bold;
    color: grey;
    background: linear-gradient(to bottom, rgba(0, 0, 0, 0.926), rgba(218, 7, 7, 0.911));
    cursor: default;
    box-shadow: 0 0 10px black;
    border-radius: 10px;
}

.Game .Game-reset-button {
    display: flex;
    flex-direction: row;
    align-items: flex-end;
    justify-content: flex-end;
    z-index: 99;
    padding-top: 1.5em;
    position: absolute;
    width: 95%;
    button {
        padding: 0.5em;
        border: none;
        background-color: #333;
        color: #fff;
        font-size: 1.2em;
        border-radius: 10px;
        box-shadow: 0 0 10px black;
        cursor: pointer;
        transition: all 0.2s ease-in-out;
        &:hover {
            background-color: #fff;
            color: #333;
        }
    }
}

.Game .Game-condition-win {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    z-index: 9999;
    font-size: 4em;
    height: 10%;
    width: 100%;
    font-weight: bold;
    background: linear-gradient(to bottom, rgba(206, 206, 206, 0.926), rgba(7, 218, 25, 0.911));
    cursor: default;
    box-shadow: 0 0 10px black;
    border-radius: 10px;
}

.Game .Game-background {
    position: absolute;
    top: 0;
    left: 0;
    height: 100vh;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    opacity: 0.7;
}

.Game .Game-person-left {
    top: 20%;
    left: 0;
    position: absolute;
    background-color: rgba(0, 0, 0, 0.288);
    border-radius: 10%;
    height: 24em;
    width: 10em;
}

.Game .Game-person-left img {
    height: 100%;
    width: 100%;
    border-radius: 10%;
}

.Game .Game-person-right {
    top: 20%;
    right: 0;
    position: absolute;
    background-color: rgba(0, 0, 0, 0.288);
    border-radius: 10%;
    height: 24em;
    width: 10em;
}

.Game .Game-person-right img {
    height: 100%;
    width: 100%;
    border-radius: 10%;
}

.Game .Game-dialog {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: relative;
    top: 70%;
    background-color: whitesmoke;
    border-radius: 0.5rem;
    box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.5);
    padding: 1rem;
}

.Game .Game-dialog .Game-dialog-name {
    font-weight: bold;
    font-size: 1.5rem;
    margin-bottom: 0.5rem;
}

.Game .Game-dialog .Game-dialog-text {
    font-size: 1.5rem;
    margin-bottom: 0.5rem;
}

.Game .Game-buttons {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    position: relative;
    top: 70%;
    border-radius: 0.5rem;
    padding: 1rem;
}

.Game .Game-buttons button {
    margin: 0 1rem;
    padding: 2rem;
    width: 30%;
    height: auto;
    font-size: 1.2rem;
    border-radius: 0.5rem;
    border: none;
    background-color: grey;
    color: white;
    cursor: pointer;
    box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.5);
    transition: all 0.2s ease-in-out;
}

.Game .Game-buttons button:hover {
    background-color: whitesmoke;
    color: #333;
    scale: 1.1;
}